.game-container{
  body {
    margin: 0;
    padding: 0;
    font-family: Arial, sans-serif;
  }
  
  .container_ {
    position: relative;
    width: 100%;
    height: 100vh;
    overflow: hidden; /* Grnmeyen tamalar gizlemek iin */
    background: #f0f0f0;
  }


.canvas {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
  
  /* Default sizes */
  .box_ {
    position: absolute;
    bottom: 0;
    background: #0d5477;
    border-radius: 5px 5px 0 0;
    margin-bottom: 5px;
  }
  
  .game-over {
    position: fixed; /* Tam ekrann ortasnda yerletirmek iin fixed kullan */
    top: 50%; /* Dikey olarak ortala */
    left: 50%; /* Yatay olarak ortala */
    transform: translate(-50%, -50%); /* Gerekten ortalanmasn sala */
    width: 100vw; /* Genilii tam ekran */
    height: 100vh; /* Ykseklii tam ekran */
    background: rgba(255, 0, 0, 0.8); /* Yar saydam arka plan */
    color: white;
    display: flex;
    flex-direction: column; /* Alt alta dizmek iin */
    align-items: center;
    justify-content: center;
    font-size: 48px; /* Font boyutunu byt */
    font-weight: bold;
    visibility: hidden; /* Varsaylan olarak gizli */
    z-index: 1000; /* Dier ieriklerin stnde grnmesini sala */
  }
  
  .game-over.visible {
    visibility: visible; /* Grnr hale getir */
  }
  
  .game-over h2 {
    margin: 0; /* Varsaylan st ve alt boluklar kaldr */
  }
  
  .restart-button {
    margin-top: 20px; /* Buton ile yaz arasnda mesafe */
    padding: 12px 25px; /* Butonun i boluu */
    font-size: xx-large; /* Font boyutu */
    color: white; /* Yaz rengi */
    background-color: black; /* Arka plan rengi */
    border: none; /* Kenarlk yok */
    border-radius: 5px; /* Kenarlar yuvarla */
    cursor: pointer; /* zerine gelince imleci deitir */
  }
  
  .restart-button:hover {
    background-color: white; /* Turuncu arka plan */
    color: black; /* Beyaz yaz rengi */
  }
  
  
  .score {
    position: absolute;
    top: 30px;
    left: 50%;
    transform: translateX(-50%);
    font-size: 24px;
    font-weight: 600;
    color: #333;
  }

  h2 {
    font-weight: 600;
    color: white!important;
  }
  
  
  .max-score {
    position: absolute;
    right: 30px;
    left: 30px;
    top: 30px;
    font-size: 24px;
    font-weight: 600;
    color: #333;
  }
  
  
  .modal-score {
    font-size: xx-large;
    font-weight: 600;
    color: white;
  }

  .fullscreen-btn {
    position: absolute;
    top: 30px;
    right: 60px;
    font-size: 24px;
    font-weight: 600;
    color: #333;
    cursor: pointer;
  }

  @media screen and (max-width: 768px) {
    .box_ {
      margin-bottom: 30px;
    }
    
  }
}